import { FC, useCallback, useEffect, useState } from 'react';
import lst from '../../../assets/styles/list/List.module.css';
import Tasks from '../../task/Task';
import todo from '../../../store/todo';
import { observer } from 'mobx-react-lite';

const List: FC = observer(() => {
    todo.getTime();

    useEffect(() => {
        setInterval(() => {
            todo.getTime();
        }, 5000) 
    }, [])
    
    return (
        <div className={lst.main}>
            <h2 className={lst.title}>{todo.time}</h2>
            <div className={lst.list}>
                <Tasks/>
            </div>
        </div>
    )
});

export default List;
