import { FC, useEffect, useState } from 'react';
import todo from '../../store/todo';
import { observer } from 'mobx-react-lite';
import tsk from '../../assets/styles/task/Task.module.css';

const Tasks: FC = observer(() => {
    const [windowSize, setWindowSize] = useState(window.innerWidth) 

    const removeTask = (id: any) => {
        todo.removeTodo(id)
    }

    window.onresize = () => setWindowSize(window.innerWidth);

    const ifLessWidth = (id: number) => {
        if (windowSize < 600) removeTask(id)
        
    }
    


    return (
        <div className={tsk.main}>
            {
                todo.tasks.map(el => 
                    <div className={tsk.task} key={el.id}> 
                        <div id={`body${el.id}`} className={tsk.task_body} onDoubleClick={() => ifLessWidth(el.id)} onTouchEnd={() => ifLessWidth(el.id)}>
                            <div id={`text${el.id}`} className={tsk.text}>{el.text}</div>
                            <div id={`time${el.id}`} className={tsk.time}>{el.time}</div>
                        </div>
                        <div className={tsk.buttons_wrapper}>
                            <button onClick={() => removeTask(el.id)}>
                            <svg className={tsk.icon} x="0px" y="0px" viewBox="0 0 507.506 507.506"  width="512" height="512">
                                <g>
                                    <path className={tsk.complete_icon} d="M163.865,436.934c-14.406,0.006-28.222-5.72-38.4-15.915L9.369,304.966c-12.492-12.496-12.492-32.752,0-45.248l0,0   c12.496-12.492,32.752-12.492,45.248,0l109.248,109.248L452.889,79.942c12.496-12.492,32.752-12.492,45.248,0l0,0   c12.492,12.496,12.492,32.752,0,45.248L202.265,421.019C192.087,431.214,178.271,436.94,163.865,436.934z"/>
                                </g>
                            </svg>
                            </button>
                            
                        </div>
                </div>
                )
            }
        </div>
    ) 
})

export default Tasks;