import { FC, useRef, useEffect } from 'react';
import inp from '../../../assets/styles/input/Input.module.css';
import { observer } from 'mobx-react-lite';
import todo from '../../../store/todo';

const Input: FC = observer(() => {

    
    const inputRef: any = useRef();
    

    const addTask = () => {
        todo.addTodo(inputRef.current.value);
        inputRef.current.value = '';
    }

    const keyAddTask = (key: any) => {
        if (key.key === 'Enter') {
            addTask();
        }
    }

    return (
        <div className={inp.main}>
            <div className={inp.name}>
                <h1 className={inp.title}>TODO <span>LIST</span></h1>
                <h3 className={inp.technology}>MobX | debouncced</h3>
            </div>
            <div className={inp.input_wrapper}>
                <input ref={inputRef} onKeyDown={(key: any) => keyAddTask(key)} type='text' placeholder='Задача'/><button onClick={() => addTask()}>ДОБАВИТЬ</button>
            </div>
        </div>
    )
})

export default Input;