import { makeAutoObservable } from 'mobx';
import moment from 'moment';
import 'moment/locale/ru'

class Todo {
    tasks = []
    time = ''
    editId = null
    node = []

    constructor () {
        makeAutoObservable(this);
    } 
    
    addTodo(taskText) {
        if (this.tasks.length == '') var key = 0;   
       
        for (let i = 1; i <= this.tasks.length; i++) var key = i; 
        
        if (taskText !== '') {
            moment.locale('ru');
            let currentTime = moment().calendar(); 
            this.tasks.push({
                            id: key, 
                            text: taskText, 
                            time: currentTime});    
        }
    }

    removeTodo(id) {
        this.tasks = this.tasks.filter(task => task.id !== id);
        for (let i = 0; i < this.tasks.length; i++) this.tasks[i].id = i;
        
    }

    getTime() {
        this.time = moment().format('LLL'); 
    } 


    addEdit(id) {
        this.editId = id;
    }
    
    reomveEdit() {
        this.editId = null;
    }

    addNode(node) {
        if (node !== null) {
            this.node = node;
        }
    }

}

export default new Todo;